import { BookDashed, Check } from 'lucide-react';
import { PageFilterConfig } from './page-group-utils';
import { Button } from '../../ui/button';
import { Slider } from '../../ui/slider';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuRadioGroup,
  DropdownMenuRadioItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '../../ui/dropdown-menu';

export interface PrintConfig {
  label: string;
  filter: PageFilterConfig | null;
  pageFormat?: { bleed?: number; [key: string]: any }; // PageFormat overrides to merge
}

export interface PrintConfigMap {
  [key: string]: PrintConfig;
}

export interface DevPrintControlsProps {
  modes?: PrintConfigMap;
  selectedMode?: string; // Current mode from parent
  onModeChange?: (mode: string, config: PrintConfig) => void;
  interactive: boolean;
  onInteractiveChange: (interactive: boolean) => void;
  hasReferenceRenderer?: boolean;
  referenceOpacity?: number;
  onReferenceOpacityChange?: (opacity: number) => void;
}

/**
 * Developer print controls - print mode toggle with optional filter dropdown
 */
export function DevPrintControls({
  modes,
  selectedMode: selectedModeProp,
  onModeChange,
  interactive,
  onInteractiveChange,
  hasReferenceRenderer = false,
  referenceOpacity = 50,
  onReferenceOpacityChange,
}: DevPrintControlsProps) {
  const modeKeys = modes ? Object.keys(modes) : [];
  const currentMode = selectedModeProp || modeKeys[0] || 'all';

  const handleModeSelect = (mode: string) => {
    // Enter print mode with selected filter
    onInteractiveChange(false);
    
    if (onModeChange && modes && modes[mode]) {
      onModeChange(mode, modes[mode]);
    }
  };

  const currentModeLabel = modes && modes[currentMode] ? modes[currentMode].label : 'All Pages';

  return (
    <DropdownMenu modal={false}>
      <DropdownMenuTrigger asChild>
        <Button
          variant="ghost"
          size="sm"
          className={`text-xs font-medium text-gray-700 hover:bg-gray-100/80 h-7 px-2.5 ${!interactive ? 'bg-gray-100/80' : ''}`}
        >
          <BookDashed className="w-3.5 h-3.5" />
          <span className="text-[10px] uppercase tracking-wide">Dev</span>
        </Button>
      </DropdownMenuTrigger>
      
      <DropdownMenuContent align="end" className="min-w-[220px]">
        <DropdownMenuLabel className="text-xs text-gray-500">Print Preview</DropdownMenuLabel>
        {modeKeys.length > 0 ? (
          <DropdownMenuRadioGroup
            value={currentMode}
            onValueChange={(value) => handleModeSelect(value)}
          >
            {modeKeys.map((key) => (
              <DropdownMenuRadioItem
                key={key}
                value={key}
                className="flex items-center gap-2"
              >
                {currentMode === key ? (
                  <Check className="w-3 h-3 text-gray-400" />
                ) : (
                  <span className="w-3 h-3" />
                )}
                {modes![key].label}
              </DropdownMenuRadioItem>
            ))}
          </DropdownMenuRadioGroup>
        ) : (
          <DropdownMenuItem
            onSelect={() => onInteractiveChange(!interactive)}
            onClick={() => onInteractiveChange(!interactive)}
            className="flex items-center gap-2"
          >
            {interactive ? <span className="w-3 h-3" /> : <Check className="w-3 h-3 text-gray-400" />}
            {interactive ? 'Enter Print Mode' : 'Exit Print Mode'}
          </DropdownMenuItem>
        )}

        {hasReferenceRenderer && (
          <>
            <DropdownMenuSeparator />
            <DropdownMenuLabel className="text-xs text-gray-500">Reference Overlay</DropdownMenuLabel>
            <div className="px-2 py-2">
              <div className="flex items-center justify-between text-xs text-gray-600">
                <span>Opacity</span>
                <span>{referenceOpacity}%</span>
              </div>
              <div className="pt-2">
                <Slider
                  value={[referenceOpacity]}
                  min={0}
                  max={100}
                  step={5}
                  onValueChange={(value) => {
                    const next = value[0] ?? referenceOpacity;
                    onReferenceOpacityChange?.(next);
                  }}
                />
              </div>
              <div className="pt-2 flex items-center justify-between text-xs text-gray-500">
                <span>Hidden</span>
                <span>Solid</span>
              </div>
            </div>
          </>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
