import { Plus, Search } from 'lucide-react';
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetDescription } from '../../ui/sheet';
import { AvailableItem, AvailableGroupItem, AvailablePageItem } from './use-page-groups';
import type { PageComponent, ReorderItem } from './page-group-utils';
import { createDefaultRenderThumbnail } from './default-render-thumbnail';
import * as React from 'react';

interface AddPageDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  availableItems: AvailableItem[];
  onSelectItem: (item: AvailableItem) => Promise<void> | void;
  pageComponents?: { [pageId: string]: PageComponent };
  payload?: any;
  setup?: { width: number; height: number };
  gridColsClass?: string;
  // MVP: No customization props, just works
}

export function AddPageDialog({
  open,
  onOpenChange,
  availableItems,
  onSelectItem,
  pageComponents,
  payload,
  setup = { width: 210, height: 297 },
  gridColsClass = "page-order-grid-cols"
}: AddPageDialogProps) {
  const [searchQuery, setSearchQuery] = React.useState('');

  const filteredItems = React.useMemo(() => {
    if (!searchQuery.trim()) return availableItems;
    const q = searchQuery.toLowerCase();
    return availableItems.filter(item =>
      (item.label || '').toLowerCase().includes(q) ||
      item.id.toLowerCase().includes(q)
    );
  }, [availableItems, searchQuery]);

  const handleSelect = (item: AvailableItem) => {
    onOpenChange(false);
    void onSelectItem(item);
  };

  const pageWidthMm = setup.width || 210;
  const pageHeightMm = setup.height || 297;
  const pageAspectRatio = pageWidthMm / pageHeightMm;
  const thumbnailWidth = 200;
  const thumbnailHeight = Math.round(thumbnailWidth / pageAspectRatio);

  const defaultRenderThumbnail = React.useMemo(() => {
    if (!pageComponents) return null;
    return createDefaultRenderThumbnail({
      pageComponents,
      payload,
      setup,
      thumbnailWidth,
      thumbnailHeight,
    });
  }, [pageComponents, payload, setup, thumbnailWidth, thumbnailHeight]);

  // Helper to evaluate pageComponentKeys (static array or function)
  const evaluatePageComponentKeys = (
    keys: string[] | ((context: { payload: any; item?: any; parent?: any }) => string[]) | undefined,
    context: { payload: any; item?: any; parent?: any }
  ): string[] => {
    if (!keys) return [];
    if (Array.isArray(keys)) return keys;
    
    // Evaluate function with error handling
    try {
      const result = keys(context);
      
      // Validate result is an array
      if (!Array.isArray(result)) {
        console.error('[uhuu-components] pageComponentKeys function must return an array, got:', typeof result);
        return [];
      }
      
      // Filter to ensure all items are strings
      const filtered = result.filter(key => typeof key === 'string');
      if (filtered.length !== result.length) {
        console.warn('[uhuu-components] pageComponentKeys returned non-string values, filtering them out');
      }
      
      return filtered;
    } catch (err) {
      console.error('[uhuu-components] Error evaluating pageComponentKeys function:', err);
      return [];
    }
  };

  const toReorderItem = (item: AvailableItem, index: number): ReorderItem => {
    if (item.kind === 'group') {
      const group = item as AvailableGroupItem;
      const context = { payload, item: undefined, parent: undefined };
      const componentKeys = evaluatePageComponentKeys(group.pageComponentKeys, context);
      const firstPageComponentKey = componentKeys[0];
      return {
        kind: 'group',
        id: item.id,
        label: item.label,
        pageCount: componentKeys.length,
        firstPageId: firstPageComponentKey,
        firstPageComponentKey,
      } as ReorderItem;
    }

    const pageItem = item as AvailablePageItem;
    const componentKey = pageItem.componentKey ?? pageItem.id;
    return {
      kind: 'page',
      id: pageItem.id,
      pageId: pageItem.id,
      pageComponentKey: componentKey,
      pageLabel: pageItem.label,
      pageNum: index + 1,
    } as ReorderItem;
  };

  return (
    <Sheet open={open} onOpenChange={onOpenChange}>
      <SheetContent side="bottom" className="h-[90vh] p-0 gap-0 w-full max-w-none bg-gray-50" data-uhuu-editor>
        <SheetHeader className="border-b border-gray-200 p-4 bg-white">
          <div className="flex items-end gap-3">
            <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center shrink-0 mb-0.5">
              <Plus className="w-4 h-4" />
            </div>
            <div className="flex-1">
              <SheetTitle className="text-base font-medium text-gray-900 leading-tight">
                Add Page or Group
              </SheetTitle>
              <SheetDescription className="text-xs text-gray-400 mt-0.5">
                Select a page or group to add to your document.
              </SheetDescription>
            </div>
            <div className="flex items-center gap-1.5 text-gray-400 mb-0.5 mr-8">
              <Search className="w-3.5 h-3.5 shrink-0" />
              <input
                type="text"
                placeholder="Filterâ€¦"
                value={searchQuery}
                onChange={e => setSearchQuery(e.target.value)}
                tabIndex={-1}
                className="w-24 bg-transparent border-0 focus:outline-none text-sm text-gray-600 placeholder:text-gray-400 focus:w-40 transition-all duration-150"
              />
            </div>
          </div>
        </SheetHeader>

        <div className="flex-1 overflow-hidden">
          <div className="h-full max-h-[calc(90vh-100px)] overflow-auto p-6">
            {filteredItems.length === 0 ? (
              <div className="text-center py-16">
                <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <Plus className="w-8 h-8 text-gray-400" />
                </div>
                <div className="text-lg font-medium text-gray-900 mb-2">No items available</div>
                <p className="text-gray-500 mb-4">{searchQuery.trim() ? 'No pages or groups match your search.' : 'All pages and groups have been added.'}</p>
              </div>
            ) : (
              <div className={gridColsClass}>
                {filteredItems.map((item, index) => {
                  const isGroup = item.kind === 'group';
                  const id = item.id;
                  const label = isGroup
                    ? (item.label || `Group ${index + 1}`)
                    : (item.label || `Page ${item.id}`);
                  const context = { payload, item: undefined, parent: undefined };
                  const pageCount = isGroup 
                    ? evaluatePageComponentKeys((item as AvailableGroupItem).pageComponentKeys, context).length
                    : 1;
                  const hasDefaultThumbnail = !!defaultRenderThumbnail;
                  return (
                    <div
                      key={id}
                      onClick={() => handleSelect(item)}
                      className={[
                        "relative w-full h-[280px] cursor-pointer transition-all group",
                        !hasDefaultThumbnail && "bg-white border-2 border-gray-200",
                        // compact + default, always base + addition if not default
                      ].filter(Boolean).join(' ')}
                    >
                      {/* Thumbnail area */}
                      <div className="w-full h-full relative">
                        {(item as any).thumbnail ? (
                          <div className="absolute inset-0 bg-gray-100 hover:bg-white">
                            <img
                              src={(item as any).thumbnail}
                              className="w-full h-full object-contain pointer-events-none object-top border border-gray-200 p-4"
                              alt={label}
                            />
                          </div>
                        ) : defaultRenderThumbnail ? (
                          <div className="absolute inset-0 flex items-center pointer-events-none">
                            {defaultRenderThumbnail(toReorderItem(item, index), index, false)}
                          </div>
                        ) : (
                          <>
                            {isGroup ? (
                              <div className="p-4">
                                <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-3">
                                  <Plus className="w-8 h-8 text-blue-600" />
                                </div>
                                <div className="text-sm font-medium text-gray-700">{label}</div>
                                <div className="text-xs text-gray-500 mt-1">
                                  {pageCount} {pageCount === 1 ? 'page' : 'pages'}
                                </div>
                              </div>
                            ) : (
                              <div className="p-4">
                                <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-3">
                                  <Plus className="w-8 h-8 text-gray-400" />
                                </div>
                                <div className="text-sm font-medium text-gray-700">{label}</div>
                                <div className="text-xs text-gray-500 mt-1">{id}</div>
                              </div>
                            )}
                          </>
                        )}
                      </div>

                      {(!defaultRenderThumbnail || item?.thumbnail) && (
                        <>        
                         <div className="absolute top-2 right-2 px-2 py-1 bg-blue-600/80 backdrop-blur-sm text-white text-xs font-medium rounded shadow-lg pointer-events-none">
                              Group ({pageCount} {pageCount === 1 ? 'page' : 'pages'})
                            </div>

                          {/* Item info overlay - positioned at bottom to match reorder dialog */}
                          <div className="absolute bottom-0 left-0 right-0 bg-black/40 backdrop-blur-sm p-3 pointer-events-none" data-item-id={id}>
                            <div className="flex items-center justify-between gap-2 text-white">
                              <div className="flex-1 min-w-0">
                                <div className="text-sm font-medium truncate">{label}</div>                                
                              </div>                             
                            </div>
                          </div>
                        </>
                      )}

                      {/* Small hover indicator */}
                      <div className="absolute top-3 left-3 w-8 h-8 bg-black rounded-full flex items-center justify-center shadow-lg opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none z-10">
                        <Plus className="w-4 h-4 text-white" />
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        </div>
      </SheetContent>
    </Sheet>
  );
}
